<template>
  <div>
    <!--  -->
    <page-header v-if="!$route.meta.hiddenHeaderContent" :title="pageTitle" class="page-header">
      <!--  -->
      <!--  <slot>  DOM 
        
      <slot name=""><slot="">name-->
      <!-- slotnameheaderContentslotpage-headernamecontent -->
      <!-- WorkplaceWorkplacePageView,WorkplacePageView
            <slot name="headerContent">WorkplaceslotWorkplacedivPageHeader<slot>
      -->
      <slot name="headerContent" slot="content"></slot>

      <!-- slotPageHeader<slot> -->
      <div slot="content">
        <p style="font-size: 14px;color: rgba(0,0,0,.65)">{{ description }}</p>
      </div>
    </page-header>
    <!--  -->
    <div class="content">
      <slot>
        <keep-alive>
          <!-- ref$refs -->
          <router-view ref="content" />
        </keep-alive>
      </slot>
    </div>
  </div>
</template>
<script>
import PageHeader from "@/components/PageHeader";
export default {
  name: "PageView",
  components: { PageHeader },
  data() {
    return {
      pageTitle: null,
      description: null
    };
  },
  props: {
    // false
    title: {
      type: [String, Boolean],
      default: true
    }
  },
  mounted() {
    this.getPageMeta();
  },
  updated() {
    this.getPageMeta();
  },
  methods: {
    getPageMeta() {
      // title
      this.pageTitle =
        typeof this.title === "string" || !this.title
          ? this.title
          : this.$route.meta.title;

      const content = this.$refs.content;

      if (content) {
        if (content.pageMeta) {
          Object.assign(this, content.pageMeta);
        } else {
          this.description = content.description;
        }
      }
    }
  }
};
</script>
<style lang="less" scoped>
.content {
  margin: 24px 24px 0;
}
.page-header {
  margin-left: 0px;
}
</style>
