<script>
export default {
  name: "SideMenuItem",
  components: {},
  props: {
    menu: {
      type: Array,
      required: true
    },
    collapsed: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  data() {
    return {};
  },
  computed: {
    activeMenu() {}
  },
  methods: {
    renderItem(menu) {
      if (!menu.hidden) {
        return menu.children && !menu.hideChildrenInMenu
          ? this.renderSubMenu(menu)
          : this.renderMenuItem(menu);
      }
    },
    // 
    renderMenuItem(menu) {
      const target = menu.meta.target || null; // 
      const tag = (target && "a") || "router-link"; //
      const props = { to: { name: menu.name } }; //props<a>
      const attrs = { href: menu.path, target: menu.meta.target }; // attrs<router-link>

      if (menu.children && menu.hideChildrenInMenu) {
        //   
        //  hidden 
        //  selectedKeys 
        menu.children.forEach(item => {
          //Object.assign() (hidden)
          item.meta = Object.assign(item.meta, {
            hidden: true
          });
        });
      }

      //const title = menu.meta.title;
      const title = `menu.${menu.meta.title}`;
      return (
        <el-menu-item index={menu.path}>
          {this.renderIcon(menu.meta.icon)}
          <span>{this.$t(`menu.${menu.meta.title}`)}</span>
        </el-menu-item>
      );
    },

    // 
    renderSubMenu(menu) {
      const itemArr = [];
      if (!menu.hideChildrenInMenu) {
        menu.children.forEach(item => {
          itemArr.push(this.renderItem(item));
        });
      }

      //
      return (
        <el-submenu index={menu.path}>
          <div slot="title">
            {this.renderIcon(menu.meta.icon)}
            <span>{this.$t(`menu.${menu.meta.title}`)}</span>
          </div>
          {itemArr}
        </el-submenu>
      );
    },
    // 
    renderIcon(icon) {
      if (icon === "none" || icon === undefined) {
        return null;
      }
      return <i class={icon}></i>;
    }
  },
  render() {
    const { menu } = this;

    const menuTree = menu.map(item => {
      if (item.hidden) {
        return null;
      }
      return this.renderItem(item);
    });

    return (
      <el-menu
        mode="vertical"
        router
        collapse={this.collapsed}
        default-active={this.$route.path}
        background-color="#001525"
        text-color="#bfcbd9"
        active-text-color="#FFFFFF"
        unique-opened={true}
        class="el-menu"
        collapse-transition={false}
      >
        {menuTree}
      </el-menu>
    );
  }
};
</script>
<style lane="less" scoped>
.el-menu {
  border-right: none !important;
}
.el-menu:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
.el-menu-item.is-active {
  background: #1585ff !important;
}
</style>
