<script>
export default {
  name: "RouteView",
  props: {
    keepAlive: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {};
  },
  render() {
    const {
      $route: { meta }
    } = this;
    const inKeep = (
      // <!-- -->
      <div>
        <div class="content">
          <router-view />
        </div>
      </div>
    );
    const notKeep = (
      <div class="content">
        <router-view />
      </div>
    );
    //  multiTab  multiTab 
    // 
    //  return meta.keepAlive ? inKeep : notKeep
    if (meta.keepAlive === false) {
      return notKeep;
    }
    return this.keepAlive || meta.keepAlive ? inKeep : notKeep;
  }
};
</script>
