<template>
  <div :style="{height:height,width:width}"></div>
</template>
<script>
export default {
  name: "Pie",
  props: {
    width: {
      type: String,
      default: "100%"
    },
    height: {
      type: String,
      default: "400px"
    }
  },
  mounted() {
    this.initChart();
  },
  methods: {
    initChart() {
      let myChart = this.$echarts.init(this.$el);
      myChart.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          orient: "horizontal",
          y: "bottom",
          data: [
            "",
            "",
            "",
            "",
            "",
            ""
          ]
        },
        series: [
          {
            type: "pie",
            radius: ["40%", "70%"],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "left"
              }
              //   emphasis: {
              //     show: true,
              //     textStyle: {
              //       fontSize: "30",
              //       fontWeight: "bold"
              //     }
              //   }
            },
            color: [
              "#fbce38",
              "#f05b72",
              "#8d58e1",
              "#3898ff",
              "#36c4c4",
              "#4ac46a"
            ],
            grid: {
              left: "0%",
              right: "0%",
              bottom: "15%",
              top: "10%"
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              { name: "", value: 32.2 },
              { name: "", value: 21 },
              { name: "", value: 17 },
              { name: "", value: 13 },
              { name: "", value: 9 },
              { name: "", value: 7.8 }
            ]
          }
        ]
      });
      setTimeout(() => {
        myChart.resize();
      }, 0);
      // 
      window.onresize = myChart.resize;
    }
  }
};
</script>
